import React, { useCallback, useEffect, useState } from "react";
import Button from "@mui/material/Button";
import Input from "@mui/material/Input";
import Snackbar from "@mui/material/Snackbar";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { useNavigate } from "react-router-dom";

import apis from "../apis/Apis";

const ariaLabel = { "aria-label": "description" };


const guList = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];

export default function Signin() {

    const navigate = useNavigate();

    // ,  
    const [userId, setUserId] = useState("");
    const [nickName, setNickName] = useState("");
    const [password, setPassword] = useState("");
    const [passwordConfirm, setPasswordConfirm] = useState("");
    const [region, setRegion] = useState("");
    const [authNumber, setAuthNumber] = useState("");

    //   
    const [userIdMessege, setUserIdMessege] = useState("");
    const [passwordMessage, setPasswordMessage] = useState("");
    const [passwordConfirmMessage, setPasswordConfirmMessage] = useState("");
    const [errorMessage, setErrorMessage] = useState("");

    //  
    const [isUserId, setIsUserId] = useState(false);
    const [isPassword, setIsPassword] = useState(false);
    const [isPasswordConfirm, setIsPasswordConfirm] = useState(false);
    const [isNickname, setIsNickName] = useState(false);
    const [isEmail, setIsEmail] = useState(false);
    //   
    const [isAuth, setIsAuth] = useState(false);

    // MUI
    const state = {
        open: false,
        errorMessage,
        vertical: "top",
        horizontal: "center",
    };

    const [snackOpen, setsnackOpen] = useState(false);

    const handleClose = () => {
        setsnackOpen(false);
    };

    const ITEM_HEIGHT = 40;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {
        PaperProps: {
            style: {
                maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
                width: 250,
            },
        },
    };

    //      
    const onSubmitHandler = (e) => {

        if (!isAuth) {
            setErrorMessage("  ");
            setsnackOpen(true);
            return
        } else if (!isPasswordConfirm) {
            setErrorMessage("  ");
            setsnackOpen(true);
            return;
        } else if (nickName.length === 0) {
            setErrorMessage(" ");
            setsnackOpen(true);
            return
        } else if (passwordConfirm.length === 0) {
            setErrorMessage("  ");
            setsnackOpen(true);
            return
        } else if (!isNickname) {
            setErrorMessage("  ");
            setsnackOpen(true);
            return
        } else {
            const UserData = {
                email: userId,
                password: password,
                passwordConfirm: passwordConfirm,
                nickname: nickName,
                location: region,
            };

            apis
                .registerUser(UserData)
                .then((response) => {
                    console.log(response);
                    setErrorMessage(" ");
                    setsnackOpen(true);
                    setTimeout(() => {
                        navigate('/login');
                    }, 1300);

                })
                .catch((error) => {
                    console.log(error);
                    setErrorMessage(" ");
                    setsnackOpen(true);
                    return;
                });
        }



    };



    //   
    const onChangeUserId = (e) => {
        const idRegex =
            /^[0-9a-zA-Z]([+=-_.]?[0-9a-zA-Z])+@+[0-9a-zA-Z]([+=-_.]?[0-9a-zA-Z])+\.+[a-zA-Z]{2,10}$/;
        const idCurrent = e.target.value;
        setUserId(idCurrent);
        if (!idRegex.test(idCurrent)) {
            setUserIdMessege("  .");
            setIsUserId(false);
        } else {
            setUserIdMessege("  .");
            setIsUserId(true);
        }
    };

    //   
    const onChangePassword = useCallback((e) => {
        const passwordRegex =
            /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,15}$/;
        const passwordCurrent = e.target.value;
        setPassword(passwordCurrent);
        if (!passwordRegex.test(passwordCurrent)) {
            setPasswordMessage(
                "++  8  15  "
            );
            setIsPassword(false);
        } else {
            setPasswordMessage("  ");
            setIsPassword(true);
        }
    }, []);

    //   
    const onChangePasswordConfirm = (e) => {
        const passwordConfirmCurrent = e.target.value;
        setPasswordConfirm(passwordConfirmCurrent);
    };

    useEffect(() => {
        if (passwordConfirm.length > 0) {
            if (password === passwordConfirm) {
                setIsPasswordConfirm(true);
                setPasswordConfirmMessage("  ");
            } else {
                setIsPasswordConfirm(false);
                setPasswordConfirmMessage("  ");
            }
        }
    }, [password, passwordConfirm]);

    //  
    const onChangeNickName = (e) => {
        setNickName(e.target.value);
    };

    const handleChange = (e) => {
        setRegion(e.target.value);
    };

    const onAuthNumber = (e) => {
        setAuthNumber(e.target.value);
    };

    //  
    const idSend = { address: userId };
    const idCheck = { email: userId };

    const onDoublingHandler = (e) => {
        e.preventDefault();
        if (!isUserId) {
            return setErrorMessage(" ");
        } else {
            apis
                .emailCheck(idCheck)
                .then((response) => {
                    console.log(response);
                    //   
                    if (response.data.success === false) {
                        setErrorMessage(response.data.errorCode.message);
                        setUserIdMessege(response.data.errorCode.message);
                        setIsUserId(false);
                        return;
                    }
                    //  
                    else {
                        //   
                        setErrorMessage(" ");
                        setIsEmail(true);
                        console.log(isEmail);
                        apis
                            .emailSend(idSend)
                            .then((response) => {
                                console.log(response);
                            })
                            .catch((error) => {
                                console.log(error);
                            });
                    }
                })
                .catch((error) => {
                    console.log(error);
                });
        }
    };

    const buttonsEmail = (
        <>
            {
                isEmail ?
                    <Button
                        style={{ marginLeft: "4%", fontSize: "x-small" }}
                        disabled
                        variant="outlined"
                        onClick={(e) => {
                            setsnackOpen(true);
                            onDoublingHandler(e);
                        }}
                    >
                         
                    </Button>
                    :
                    <Button
                        style={{ marginLeft: "4%", fontSize: "x-small" }}
                        variant="outlined"
                        onClick={(e) => {
                            setsnackOpen(true);
                            onDoublingHandler(e);
                        }}
                    >
                         
                    </Button>
            }
        </>
    );

    const certification = {
        email: userId,
        certificationNum: authNumber,
    };
    const onAuthNumberHandler = (e) => {
        e.preventDefault();
        if (authNumber.length === 0) {
            return setErrorMessage(" ");
        } else {
            apis
                .certification(certification)
                .then((response) => {
                    console.log(response.data.success);
                    if (!response.data.success) {
                        return setErrorMessage(" ");
                    }
                    setErrorMessage(" ");
                    setIsAuth(true);
                })
                .catch((error) => {
                    console.log(error);
                });
        }
    };

    const buttonsAuthNumber = (
        <>
            {isAuth ? (
                <Button
                    disabled
                    style={{ marginLeft: "4%", fontSize: "x-small" }}
                    variant="outlined"
                    onClick={(e) => {
                        setsnackOpen(true);
                        onAuthNumberHandler(e);
                    }}
                >
                     
                </Button>
            ) : (
                <Button
                    style={{ marginLeft: "4%", fontSize: "x-small" }}
                    variant="outlined"
                    onClick={(e) => {
                        setsnackOpen(true);
                        onAuthNumberHandler(e);
                    }}
                >
                    
                </Button>
            )}
        </>
    );

    //   
    const checkNickname = { nickname: nickName };
    const onDoublingNickHandler = (e) => {
        e.preventDefault();
        if (nickName.length === 0) {
            return setErrorMessage(" ");
        } else {
            apis
                .nicknameCheck(checkNickname)
                .then((response) => {
                    console.log(response);
                    if (response.data.success) {
                        setIsNickName(true);
                        setsnackOpen(true);
                        setErrorMessage("  ");
                    } else {
                        setsnackOpen(true);
                        setErrorMessage("  ");
                    }
                })
                .catch((error) => {
                    console.log(error);
                });
        }
    };

    const buttonsNickname = (
        <>
            {isNickname ? (
                <Button
                    disabled
                    style={{ marginLeft: "4%", fontSize: "x-small" }}
                    variant="outlined"
                    onClick={(e) => {
                        setsnackOpen(true);
                        onDoublingNickHandler(e);
                    }}
                >
                     
                </Button>
            ) : (
                <Button
                    style={{ marginLeft: "4%", fontSize: "x-small" }}
                    variant="outlined"
                    onClick={(e) => {
                        setsnackOpen(true);
                        onDoublingNickHandler(e);
                    }}
                >
                     
                </Button>
            )}
        </>
    );

    return (
        <div
            style={{
                margin: "auto",
                width: "80vw",
                height: "100%",
                marginTop: "2vh",
                padding: "3vh",
                boxShadow: "rgba(0, 0, 0, 0.1) 0px 4px 12px",
            }}
        >
            <Snackbar
                anchorOrigin={{
                    vertical: "top",
                    horizontal: "center",
                }}
                open={snackOpen}
                autoHideDuration={2000}
                message={errorMessage}
                onClose={handleClose}
                key={state.vertical + state.horizontal}
            ></Snackbar>
            <h2></h2>
            <form>

                <p></p>
                {isEmail ? (
                    <Input
                        disabled
                        defaultValue="Disabled"
                        style={{ width: "68%" }}
                        placeholder=" "
                        inputProps={ariaLabel}
                        onChange={onChangeUserId}
                    />
                ) : (
                    <Input
                        style={{ width: "68%" }}
                        placeholder=" "
                        inputProps={ariaLabel}
                        onChange={onChangeUserId}
                    />
                )
                }
                {buttonsEmail}

                <br />
                {isEmail && (
                    <>
                        {isAuth ? (
                            <Input
                                disabled
                                defaultValue="Disabled"
                                style={{ width: "68%" }}
                                placeholder=" "
                                inputProps={ariaLabel}
                                onChange={onAuthNumber}
                            />
                        ) : (
                            <Input
                                style={{ width: "68%" }}
                                placeholder=" "
                                inputProps={ariaLabel}
                                onChange={onAuthNumber}
                            />
                        )}
                        {buttonsAuthNumber}

                    </>
                )}
                <br />
                {userId.length > 1 && isUserId ? (
                    <>
                        {isAuth ? (
                            <span style={{ color: "green" }}>
                                  
                            </span>
                        ) : (
                            <span style={{ color: "green" }}>{userIdMessege}</span>
                        )}
                    </>
                ) : (
                    <span style={{ color: "red" }}>{userIdMessege}</span>
                )}


                <p></p>
                <Input
                    fullWidth
                    type="password"
                    placeholder=" "
                    onChange={onChangePassword}
                />
                <br />
                {
                    isPassword ? (
                        <span style={{ color: "green" }}>{passwordMessage}</span>
                    ) : (
                        <span style={{ color: "red" }}>{passwordMessage}</span>
                    )
                }

                <p> </p>
                <Input
                    fullWidth
                    type="password"
                    placeholder="   "
                    onChange={onChangePasswordConfirm}
                />
                <br />
                {passwordConfirm.length > 0 && !isPasswordConfirm ? (
                    <span style={{ color: "red" }}>{passwordConfirmMessage}</span>
                ) : (
                    <span style={{ color: "green" }}>{passwordConfirmMessage}</span>
                )}
                <p />

                <p></p>
                {isNickname ? (
                    <Input
                        disabled
                        defaultValue="Disabled"
                        style={{ width: "68%" }}
                        type="text"
                        placeholder=" "
                        onChange={onChangeNickName}
                    />
                ) : (
                    <Input
                        style={{ width: "68%" }}
                        type="text"
                        placeholder=" "
                        onChange={onChangeNickName}
                    />
                )}

                {buttonsNickname}


                <FormControl
                    variant="standard"
                    sx={{ width: "100%", paddingBottom: "10px", marginTop: "10px" }}
                >
                    <InputLabel id="demo-simple-select-standard-label">
                         
                    </InputLabel>
                    <Select
                        labelId="demo-simple-select-standard-label"
                        id="demo-simple-select-standard"
                        value={region}
                        onChange={handleChange}
                        label="region"
                        MenuProps={MenuProps}
                    >
                        <MenuItem value="">
                            <em>--- ---</em>
                        </MenuItem>
                        {guList.map((item, idx) => {
                            return <MenuItem value={item} key={idx}>{item}</MenuItem>
                        })}
                    </Select>
                </FormControl>
                <br />
                <div>
                    <Button fullWidth variant="contained" onClick={(e) => { onSubmitHandler(e) }} ></Button>
                </div>
            </form>
            <div style={{ textAlign: "right", marginTop: "10px" }}>
                <Button variant="text" onClick={() => { navigate('/login') }}>
                    
                </Button>
            </div>
        </div>
    );
}
