import { useState } from "react";
import Header from "../components/Header";
import apis from "../apis/Apis";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { useNavigate } from "react-router-dom";
import Post from "../components/Post";
import Select from "@mui/material/Select";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";

import SearchRoundedIcon from "@mui/icons-material/SearchRounded";

export default function SearchPage() {
  const navigate = useNavigate();
  const [search, setSearch] = useState({ title: "", category: "" });
  const [searchList, setSearchList] = useState([]);
  const onSearch = (e) => {
    setSearch({ ...search, title: e.target.value });
  };

  const onCategory = (e) => {
    setSearch({ ...search, category: e.target.value });
  };

  console.log(search);
  const Click = () => {
    apis
      .searchList(search)
      .then((response) => {
        setSearchList(response.data.data.content);
        console.log(response);
      })

      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <>
      <Header>
        <ArrowBackIcon
          style={{ fontSize: "25px" }}
          onClick={() => {
            navigate("/");
          }}
        ></ArrowBackIcon>
      </Header>

      {/*   */}
      <Box sx={{ padding: "10px 20px" }}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "15px",
          }}
        >
          <TextField
            style={{ width: "69vw" }}
            id="outlined-basic"
            placeholder=" "
            variant="outlined"
            onChange={onSearch}
          />
          <Button
            style={{
              fontSize: "10px",
              backgroundColor: "#FFBA46",
              borderRadius: "5px",
            }}
            variant="contained"
            onClick={Click}
          >
            
          </Button>
        </Box>
        <FormControl
          variant="standard"
          sx={{ width: "70%", textAlign: "center" }}
        >
          <Select
            labelId="demo-simple-select-standard-label"
            id="demo-simple-select-standard"
            label=""
            value={search.category}
            onChange={onCategory}
            displayEmpty
          >
            <MenuItem value=""> </MenuItem>
            <MenuItem value="DEVICE"></MenuItem>
            <MenuItem value="APPLANCE"></MenuItem>
            <MenuItem value="KITCHEN">/</MenuItem>
            <MenuItem value="WOMEN">/</MenuItem>
            <MenuItem value="MEN">/</MenuItem>
            <MenuItem value="BEAUTY">/</MenuItem>
            <MenuItem value="GAME">/</MenuItem>
            <MenuItem value="BOOK"></MenuItem>
            <MenuItem value="TICKET"></MenuItem>
          </Select>
        </FormControl>
      </Box>

      {/*     */}
      <Box sx={{ height: "70vh", overflow: "auto", padding: "20px" }}>
        {searchList[0] === undefined && (
          <div style={emptyCss}>
            <SearchRoundedIcon
              sx={{ width: "150px", height: "150px", color: "gray" }}
            />
            <p>  : )</p>
          </div>
        )}
        {searchList.map((post, idx) => {
          return <Post post={post} key={idx}></Post>;
        })}
      </Box>
    </>
  );
}

const emptyCss = {
  height: "100%",
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
};
